"use client";
import socket from "../lib/socket";
import WaitingLobby from "../components/WaitingLobby";
import { userGamestore } from "../store/userGamestore";
import { useEffect, useState } from "react";
import GameRoom from "../components/room/GameRoom";
import type { GameState } from "../types/game";
import { useRouter } from "next/navigation";
import { getClientId } from "../page"; // Ensure this path is correct

export default function RoomPage() {
  const router = useRouter();
  const { gameState, setGameState } = userGamestore();
  const [commentary, setCommentary] = useState<string | null>(null);

  // 1. HANDLE GAME EVENTS & ERRORS
  useEffect(() => {
    const handleGameStateUpdate = (state: GameState) => {
      console.log("Game state updated:", state);
      setGameState(state);
      // Save valid room ID for valid reconnections later
      localStorage.setItem("lastRoomId", state.roomId);
    };

    const handleGameEnded = ({
      gameState,
      commentary,
    }: {
      gameState: GameState;
      commentary: string;
    }) => {
      setGameState(gameState);
      setCommentary(commentary);
    };

    const handleGameError = (err: string) => {
      console.error("Game error received:", err);

      // CRITICAL FIX: If game is not found (stale ID), clear storage and go home
      if (err === "game not found" || err.includes("not found")) {
        console.warn(
          "Invalid room detected. Clearing storage and redirecting."
        );
        localStorage.removeItem("lastRoomId");
        router.push("/");
      }
    };

    socket.on("gameStateUpdate", handleGameStateUpdate);
    socket.on("gameEnded", handleGameEnded);
    socket.on("gameError", handleGameError);

    return () => {
      socket.off("gameStateUpdate", handleGameStateUpdate);
      socket.off("gameEnded", handleGameEnded);
      socket.off("gameError", handleGameError);
    };
  }, [setGameState, router]);

  // 2. HANDLE RECONNECTION (On Refresh Only)
  useEffect(() => {
    // Only try to reconnect if we have NO state but DO have a stored ID
    const lastRoomId = localStorage.getItem("lastRoomId");
    if (!gameState && lastRoomId) {
      console.log("Attempting to reconnect to:", lastRoomId);
      socket.emit("reconnectRoom", {
        roomId: lastRoomId,
        clientId: getClientId(),
      });
    }
  }, [gameState]); // Keep this dependency to check only when state is missing

  // 3. HANDLE CLEANUP / LEAVE ROOM
  useEffect(() => {
    const lastRoomId = localStorage.getItem("lastRoomId");

    // If we have no state and no stored ID, we shouldn't be here -> Go Home
    if (!gameState && !lastRoomId) {
      router.push("/");
    }

    // Cleanup function: ONLY runs when component unmounts (Refresh/Back Button)
    return () => {
      // We check if gameState exists roughly to see if we were in a game
      // (Using the ref or just firing leaveRoom safely)
      if (gameState?.roomId) {
        console.log("Leaving room...");
        socket.emit("leaveRoom", {
          roomId: gameState.roomId,
          clientId: getClientId(),
        });
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // <--- CRITICAL FIX: Empty dependency array prevents kicking player on every render

  // 4. RENDER LOADING STATE
  if (!gameState)
    return (
      <div className="text-center p-10 text-fg">
        <p>Loading room...</p>
        <button
          onClick={() => {
            localStorage.removeItem("lastRoomId");
            router.push("/");
          }}
          className="mt-4 text-sm text-purple underline cursor-pointer"
        >
          Stuck? Click here to go Home
        </button>
      </div>
    );

  const currentPlayer = gameState.players.find(
    (p) => p.clientId === getClientId()
  );
  const isEliminated = currentPlayer?.isEliminated;

  switch (gameState.status) {
    case "LOBBY":
      return (
        <WaitingLobby
          players={gameState.players}
          roomId={gameState.roomId}
          maxPlayers={gameState.maxPlayers}
        />
      );
    case "INGAME":
      return (
        <div className="relative w-full h-full">
          {isEliminated && (
            <div className="fixed top-0 left-0 w-full h-16 bg-red-600/90 text-white z-50 flex items-center justify-center font-bold text-xl animate-pulse">
              YOU HAVE BEEN DISQUALIFIED!
            </div>
          )}
          <div
            className={
              isEliminated ? "opacity-75 pointer-events-none grayscale" : ""
            }
          >
            <GameRoom />
          </div>
        </div>
      );
    case "FINISHED":
      return (
        <div className="flex flex-col items-center justify-center min-h-screen p-10 text-center space-y-6">
          <h1 className="text-6xl font-black text-(--color-pink)">GAME OVER</h1>
          <div className="text-2xl">
            Winner:{" "}
            <span className="font-bold text-green-400">{gameState.winner}</span>
          </div>
          {commentary && (
            <div className="bg-(--color-line) p-6 rounded-xl border border-(--color-purple) max-w-2xl mt-8">
              <h3 className="text-sm uppercase tracking-widest text-(--color-comment) mb-2">
                AI Commentator
              </h3>
              <p className="text-xl italic font-serif leading-relaxed">
                "{commentary}"
              </p>
            </div>
          )}

          <button
            onClick={() => {
              localStorage.removeItem("lastRoomId"); // Clean up so we don't reconnect to finished game
              router.push("/");
            }}
            className="px-6 py-3 bg-(--color-purple) rounded-lg font-bold hover:scale-105 transition"
          >
            Back to Home
          </button>
        </div>
      );
    default:
      return <div>Unknown game status!</div>;
  }
}
